:root {
  --primary-color: #00acdc;
}

html {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-size: .75rem;
  font-weight: 500;
}

* {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.navigation__list {
  list-style-type: none;
  display: flex;
}

.list__item {
  text-transform: uppercase;
  height: 100%;
  position: relative;
}

.list__item:not(:first-child) {
  margin-left: 20px;
}

.list__link {
  color: #000;
  height: 60px;
  font-weight: bold;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.link:hover, .list__link:hover {
  color: var(--primary-color);
}

.is-active {
  color: var(--primary-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--primary-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
